import React, { useEffect, useState } from "react";
import { BrowserRouter as Router } from 'react-router-dom';
import Loader from "./components/Load/index.js";
//import Home from "./home/index";
import Login from "./components/Login/index.js"
import './css/App.css';
const { readFileSync, writeFileSync, existsSync } = require('fs');
//import fs from "fs";

function App() {
	const [appLoaded, setAppLoaded] = useState(false);
	const [startLoadProgress, setStartLoadProgress] = useState(false);
	//const [login, setlogin] = useState(false);
	//const [authjson, setauthjson] = useState();

	const stopLoad = () => {
		setStartLoadProgress(true);
		setTimeout(() => setAppLoaded(true), 3000);
	};

	useEffect(() => {
		stopLoad();
	}, []);
	
	if (existsSync('./Keerthana.json')) {
        return localStorage.setItem('login', 'true')
    } else
	{
		return	localStorage.setItem('login', 'false')
	}
	console.log(localStorage.getItem('login'))

	if (!appLoaded) return <Loader done={startLoadProgress} />;


	return (
		<div className="app">	
		<p className="app__mobile-message"> Only available on desktop. </p> 
			<Router>
				<div className="app-content">
					<Login />
				</div>
			</Router>
		</div>
	);
}

export default App;